import React from 'react';

import './FilterButtons.sass';

class FilterButtons extends React.Component {
constructor(props) {
    super(props);
    this.state = {
      value: "",
    };
};

handleClick(value, event) {
  console.log('click ',value);
  this.props.onChange(value);
}

render() {
    const filterButtons = this.props.options.map(option =>
      <li><a href='#' className={'filterButtons__button'} onClick={e => this.handleClick(option, e)} > { option } </a> </li>
    );

    return (
      <div id={"filters"}>
          <ul>
          <li className={"active"} onClick={e => this.handleClick('', e)}><a href={"#"}>All rockets</a></li>
            {filterButtons}
          </ul>
      </div>
    );
  }
}

export default FilterButtons;
